import { useState } from "react";
import receiveFetch from "../utils/receiveFetch"
import styled from "styled-components";
import esnlogo from "./image3.png";

const Wrapper = styled.div`
  	display: flex;
    min-height: 100vh;    
    background: linear-gradient(to right top, #c8d8e4, #52ab98);
    justify-content: space-around;
    align-items: center;
    @media (max-width: 726px) {
    	flex-direction: column;
    }
`;

const Image = styled.img`
  	object-fit: contain;
    height: 15rem;
    width: 30rem;
	  z-index: 1;
`;

const Loginform = styled.form`
  z-index: 1;
  margin: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 5rem;
`;

const Title = styled.h1`
    text-align: center;
    width: 80%;
    margin-bottom: 2rem;
    border-bottom: solid 0.4rem #4B5C77;
`;

const Formfield = styled.div`
  position: relative;
  margin: 1.5rem;
  font-size: 5rem;
  
  input {
    appearance: none;
    border: none;
    border-bottom: 0.1rem solid #000;
    outline: none;
    transition: 300ms;

    :focus {
      border-bottom-color:#3498db;
    }
  }
`;

const Label = styled.label`
    color: #000;
    position: absolute;
    left: 0;
    bottom: 10%;
    transition: 300ms;

    ${({active}) => active && `font-size: 2rem; top: -1rem;`};
`;

const Button = styled.input`
    margin: 3rem;
    padding: 0.5rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    border-radius: 1rem;
    border: none;
    background: #4B5C77;
    color: white;

    :hover {
        background: #525255;
    }
`;

const ErrorMessage = styled.p`
    font-size: 2.5rem;
    color: red;
`;

const Baloon = styled.div`
	border-radius: 50%;
	background: linear-gradient(to right top, #c8d8e4, #52ab98);
	height: 15rem;
	width: 15rem;
	position: absolute;
`;

const Baloonright = styled(Baloon)`
	top: 25%;
	right: 25%;
`;

const Baloonleft = styled(Baloon)`
	bottom: 25%;
	left: 25%;
`;

const Login = () => {

  const [ list, setList ] = useState(new Set())
  const [loginfo, setLoginfo] = useState({ username: "", password: "" });
  const [errorinfo, setErrorinfo] = useState();

  async function sendLoginfo(e) {
    e.preventDefault()
    const res = await receiveFetch("/auth/login", "POST", loginfo)
    if (res === loginfo.username) {
      localStorage.setItem("user", res);
      window.location.href="/"
    } else {
      setErrorinfo(res.message);
    }
  }

  function handleInput(e) {
    const { name, value } = e.target;
    setLoginfo(prev => ({ ...prev, [name]: value }));
  }

  function addToList(e) {
    setList(prevInfo => new Set(prevInfo.add(e.target.name)))
  }

  function removeFromList(e) {
    if (!e.target.value) setList(prev => new Set([...prev].filter(x => x != e.target.name)))
  }

  return (
    <Wrapper>
		  <Baloonright />
	    <Baloonleft />
      <Image src={esnlogo}></Image>
      <Loginform onSubmit={sendLoginfo}>
        <Title>Prijava</Title>
        <Formfield>
          <input 
            autoComplete="off"
            type="text"
            onChange={handleInput}
            onFocus={addToList} 
            onBlur={removeFromList}
            name="username" 
            id="username"
          />
          <Label htmlFor="username" active={list.has("username")}>Korisničko ime:</Label>
        </Formfield>
        <Formfield>
          <input 
            type="password"
            onFocus={addToList} 
            onChange={handleInput}
            onBlur={removeFromList}
            name="password" 
            id="password"
          />
          <Label htmlFor="password" active={list.has("password")}>Lozinka:</Label>
        </Formfield>
        <Button value="Prijava" type="submit" />
        <ErrorMessage>{errorinfo ? `${errorinfo}*` : ""}</ErrorMessage>
      </Loginform>
    </Wrapper>
  );
};

export default Login;
