import { useState, useEffect, useContext } from "react";
import { useParams } from "react-router-dom"
import styled from "styled-components";
import { UserlistContext } from "../../Context/Context";
import sendFetch from "../../utils/sendFetch"
import receiveFetch from "../../utils/receiveFetch"

const Formstyled = styled.form`
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background: #c8d8e4;
`;

const Div = styled.div`
    display: flex;
    font-size: 4rem;
    flex-direction: column;
`;

const Input = styled.input`
    font-size: 3rem;
`;

const Addedclan = styled.div`
    font-size: 2rem;
    margin: 0.5rem;
    border: 0.1rem gray solid;
    ${({ active }) => active && `background-color: green;`}
`;

const Eventform = () => {

    const { id } = useParams()

    const { userlist } = useContext(UserlistContext)
    
    const [ filtered, setFiltered ] = useState([])

    const [ clanovi, setClanovi ] = useState([])

    const [ eventinfo, setEventinfo ] = useState({
        ime_eventa: "",
        ime_organizatora: "",
        datum: "",
        cijena: "",
        tim: "",
    })

    const { ime_eventa, ime_organizatora, datum, cijena, tim } = eventinfo

    const timovi = [ "HR", "Party", "Partnership", "SI", "HWB", 
                     "ICE", "Buddy", "PR", "Travel", "Projektni" ]

    useEffect(() => {
        setFiltered(userlist)

        async function fetchData() {
            const res  = await receiveFetch("/api/get_eventform", "POST", { id })
            setEventinfo(res)
            if (res.json_agg[0]) setClanovi(res.json_agg)
        }
        if (id) fetchData()
    }, [userlist])

    function handleBox(e) {
        setEventinfo(prev => ({ ...prev, [e.target.name]: e.target.value}))
    }

    function pushClan(e) {
        const clan_id = Number(e.target.getAttribute("name"))

        if (clanovi.includes(clan_id)) setClanovi(clanovi.filter(c => c !== clan_id));
        else setClanovi(prev => [ ...prev, clan_id ])
    }
    
    function sendEventinfo(e) {
        e.preventDefault()

        const info = [ eventinfo, clanovi ] 

        if (id) sendFetch("/api/update_event", "POST", info)
        else sendFetch("/api/create_event", "POST", info)
        window.location.href = "/eventi" 
    }

    function Searchbar(e) {
        const search = userlist.filter(elem => {
            const string = `${elem.ime} ${elem.prezime}`
            return string.toLowerCase().includes(e.target.value) || 
                   string.toUpperCase().includes(e.target.value)
        })

        setFiltered(search)
    }

    return (
        <Formstyled onSubmit={sendEventinfo}>
            <Div>
                <label htmlFor="ime_eventa">Ime eventa:</label>
                <input onChange={handleBox} type="text" id="ime_eventa" name="ime_eventa" value={ime_eventa}/>

                <label htmlFor="ime_organizatora">Ime organizatora:</label>
                <input onChange={handleBox} type="text" id="ime_organizatora" name="ime_organizatora" value={ime_organizatora}/>

                <label htmlFor="cijena">Cijena:</label>
                <input onChange={handleBox} type="number" id="cijena" name="cijena" value={cijena}/>
            </Div>

            <Div>
                <label htmlFor="datum">Datum održavanja:</label>
                <input onChange={handleBox} type="date" id="datum" name="datum" value={datum}/>
            </Div>

            <Div>
                <label htmlFor="tim">Odaberi tim::</label>

                <select value={tim || "default"} onChange={handleBox} name="tim" id="tim">                
                    <option value="default" disabled> -- odaberi opciju -- </option>
                    {timovi.map(tim => <option value={tim}>{tim}</option> )}
                </select>
            </Div>

            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />

            <Div>
            {filtered && filtered.length ? filtered.map(user => {
                return (
                    <Addedclan 
                        active={clanovi.includes(user.id)} 
                        onClick={pushClan} 
                        name={user.id} 
                        key={user.id}
                    >
                        {user.ime} {user.prezime} {user.tim} {user.tel}
                    </Addedclan>
                )
            })
            :
            <Div>
                Nema članova za prikazati...
            </Div>
            }
            </Div>

            <Input value="Spremi" type="Submit"/>
        </Formstyled>
    )
}

export default Eventform;
