import { Link } from "react-router-dom"
import styled from "styled-components"

const Wrapper = styled.div`
    font-size: 5rem;
    width: 100%;
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: center;
    background: #c8d8e4;
`;

const Styledlink = styled(Link)`
    flex-basis: 50%;
    text-align: center;
    padding: 2rem;
    text-decoration: none;
    color: black;
`;

const Svitimovi = () => {

    const timovi = [ "HR", "Party", "Partnership", "SI", "HWB", 
                     "ICE", "Buddy", "PR", "Travel", "Projektni" ]

    return (
        <Wrapper>
            {timovi.map(tim => {
                return (
                    <Styledlink 
                        key={tim}
                        to={{pathname: `/tim/${tim}`}}
                    >
                        {tim}
                    </Styledlink>
                )
            })}
        </Wrapper>
    )
}

export default Svitimovi;
